@font-face {
  font-family: "roboto-example";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/RobotoRegular/RobotoRegular.eot");
  src: url("../fonts/RobotoRegular/RobotoRegular.eot?#iefix") format("embedded-opentype"), url("../fonts/RobotoRegular/RobotoRegular.woff") format("woff"), url("../fonts/RobotoRegular/RobotoRegular.ttf") format("truetype");
}

@font-face {
  font-family: "roboto-example";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/RobotoBold/RobotoBold.eot");
  src: url("../fonts/RobotoBold/RobotoBold.eot?#iefix") format("embedded-opentype"), url("../fonts/RobotoBold/RobotoBold.woff") format("woff"), url("../fonts/RobotoBold/RobotoBold.ttf") format("truetype");
}

.page-header {
  background-image: url(../img/All/back-desktop.png);
  background-position: top center;
  background-repeat: no-repeat;
  position: relative;
  z-index: 1;
}

.top-header__wrapper {
  background-color: rgba(21, 28, 36, 0.7);
  margin-bottom: 99px;
  min-height: 100px;
  box-sizing: border-box;
  padding-top: 20px;
}

.logo-navigation__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.page-header__logo {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.33333% - 30px);
  align-self: center;
  fill: #ffffff;
  cursor: pointer;
}

.page-header__logo:hover {
  fill: #d22856;
}

.page-header__logo:active {
  fill: rgba(210, 40, 86, 0.9);
}

.main-page__nav {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(66.66667% - 30px);
}

.main-page__nav .hamburger {
  display: none;
}

.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
  background-color: #ffffff;
  width: 50px;
}

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  background-color: #ffffff;
}

.site-list {
  display: flex;
  list-style: none;
  justify-content: space-around;
}

.site-list__logo {
  display: none;
}

.site-list__item--header {
  flex-basis: 100px;
}

.site-list__item a {
  color: #ffffff;
  text-decoration: none;
}

.site-list__item a:hover {
  color: #d22856;
}

.site-list__item a:active {
  color: rgba(210, 40, 86, 0.9);
}

.page-header__bottom-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.main-page__left-wrapper {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(50% - 30px);
}

.iphone-hand__wrapper {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(50% - 30px);
}

.download-block__wrapper {
  max-width: 320px;
}

.main-page__description {
  box-sizing: border-box;
  max-width: 250px;
  padding: 0;
  margin: 0;
  margin-left: 30px;
  margin-bottom: 55px;
  color: #ffffff;
  font-size: 50px;
  line-height: 1.2;
  font-weight: 300;
  text-align: center;
}

.download-block__button {
  display: block;
  width: 300px;
  padding: 16px 0;
  margin-bottom: 65px;
  border: 3px solid #ffffff;
  border-radius: 40px;
  color: #ffffff;
  text-decoration: none;
  text-align: center;
  font-size: 21px;
  font-weight: 700;
  line-height: 18px;
}

.download-block__button:hover {
  background-color: #ffffff;
  color: #d22856;
}

.download-block__button:active {
  background-color: #ffffff;
  color: rgba(210, 40, 86, 0.9);
}

.download-block__img--iphone {
  margin-left: 47px;
}

.download-block__img--android {
  margin-left: 35px;
}

.download-block__img--wp {
  margin-left: 35px;
}

.download-block__deacription {
  margin: 0;
  margin-top: 61px;
  margin-bottom: 115px;
  padding: 0;
  color: #ffffff;
  font-size: 18px;
  font-weight: 300;
  line-height: 24px;
  text-align: center;
}

.iphone-hand__image {
  position: absolute;
  bottom: 0;
  right: 15%;
  z-index: -1;
}

.proposals {
  padding-top: 60px;
  padding-bottom: 67px;
  background-image: url(../img/All/proposals-bg.png);
  background-position: center top 276px;
  background-repeat: no-repeat;
  background-size: cover;
}

.proposals__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.proposals-title {
  font-size: 50px;
  font-weight: 300;
  color: #283645;
  margin: 0 auto;
  display: block;
}

.proposals-title--first {
  text-align: center;
}

.proposals-title--second {
  width: 640px;
  margin-bottom: 75px;
  font-size: 45px;
  text-align: center;
  box-sizing: border-box;
  line-height: normal;
}

.proposals-block__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-around;
  margin-bottom: 53px;
  position: relative;
}

.proposals-block__wrapper::before {
  display: none;
  content: '';
  background-image: url(../img/All/iphone-img3.png);
  background-repeat: no-repeat;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 200px;
  height: 281px;
  min-height: 320px;
  min-width: 157px;
}

.proposals-block__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.proposals-block__img {
  margin-bottom: 30px;
}

.proposals-block__item::after {
  position: absolute;
  content: '';
  top: 264px;
  left: -1px;
  width: 238px;
  height: 4px;
  background-color: rgba(212, 212, 212, 0.2);
}

.mood-block::before {
  display: none;
  position: absolute;
  content: '';
  height: 500px;
  width: 500px;
  top: -35px;
  left: -280px;
  background-image: url(../img/All/iphone-img3.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.mood-block__img {
  width: 164px;
  height: 264px;
}

.changes-block__img {
  width: 164px;
  height: 264px;
}

.friends-block__img {
  width: 164px;
  height: 264px;
}

.proposals-block__title {
  width: 236px;
  box-sizing: border-box;
  text-align: center;
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;
}

.proposals-block__subtitle {
  width: 236px;
  min-height: 146px;
  text-align: center;
  font-size: 18px;
  font-weight: 400;
  color: #283645;
}

.effect-block__wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.effect-block__img-wrap {
  margin-left: 70px;
}

.effect-block__text-wrap {
  width: 525px;
  margin-left: 71px;
}

.effect-block__title {
  margin: 0;
  font-size: 30px;
  font-weight: 700;
  line-height: 30px;
  margin-bottom: 25px;
}

.effect-block__description {
  margin: 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  color: #283645;
}

.effect-block__description--first {
  margin-bottom: 45px;
}

.effect-block__description-link {
  color: #283645;
}

.reviews {
  padding-top: 193px;
  padding-bottom: 35px;
}

.reviews::before {
  position: absolute;
  content: url(../img/All/quotes.png);
  left: 50%;
  transform: translateX(-50%);
  margin-top: -135px;
  width: 79px;
  height: 63px;
}

.reviews-slider__wrapper {
  position: relative;
}

.review-slider__items {
  position: relative;
  z-index: 1000;
}

.review-slider__icon svg {
  position: absolute;
  display: block;
  left: 50%;
  top: -121px;
  transform: translateX(-50%);
}

.review-slider__description {
  position: relative;
  max-width: 70%;
  margin: 0 auto;
  margin-bottom: 44px;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  text-align: center;
}

.review-slider__name {
  margin: 0;
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  color: #000;
}

.review-slider__name-sm {
  display: none;
}

.review-slider__name-description {
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  color: #283645;
}

.slick-next {
  height: 41px;
  width: 22px;
  right: 50px;
  top: 80px;
}

.slick-prev {
  height: 41px;
  width: 22px;
  left: 50px;
  top: 80px;
  z-index: 1;
}

.slick-prev::before {
  content: url(../img/All/left-arrow.png);
}

.slick-next::before {
  content: url(../img/All/right-arrow.png);
}

.slick-dots {
  bottom: -30px;
}

.slick-dots li button::before {
  margin-top: 24px;
  opacity: 1;
  font-size: 0;
  width: 6px;
  height: 6px;
  background-color: #bec3c7;
  border-radius: 50%;
}

.slick-dots li.slick-active button:before {
  border: 3px solid #283645;
  transform: translateY(-3px);
}

.price {
  padding-top: 159px;
  padding-bottom: 98px;
  background-image: url(../img/All/proposals-bg.png);
}

.price-table {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  flex-wrap: nowrap;
  justify-content: center;
}

.price-item {
  flex-grow: 1;
}

.price-item__head {
  min-height: 98px;
}

.price-item__head {
  background-color: #283645;
}

.price-item__head--transparent {
  background-color: transparent;
}

.price-item__head--hit {
  position: relative;
}

.price-item__head--hit::before {
  position: absolute;
  content: url(../img/All/hit.png);
  top: 0;
  left: 0;
  width: 74px;
  height: 73px;
}

.price-item__head--border {
  border-radius: 3px 0 0 3px;
}

.price-body__row {
  box-sizing: border-box;
  background-color: #ffffff;
  text-align: center;
  min-height: 63px;
  flex-grow: 1;
  padding-top: 20px;
  border-top: 2px solid #e5e5e5;
  border-left: 2px solid #e5e5e5;
}

.price-body__row--padding {
  padding-top: 16px;
}

.price-body__row--border-b {
  border-bottom: 2px solid #e5e5e5;
}

.price-body__row--border-r {
  border-right: 2px solid #e5e5e5;
}

.price-body__row--table {
  border-left: 2px solid #e5e5e5;
  border-right: 2px solid #e5e5e5;
  border-top: 2px solid #e5e5e5;
}

.price-head__name {
  display: block;
  padding-top: 16px;
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
  text-transform: uppercase;
  color: #ffffff;
  text-align: center;
}

.price-head__description {
  display: block;
  font-size: 24px;
  font-weight: 300;
  line-height: 30px;
  text-transform: uppercase;
  color: #ffffff;
  text-align: center;
}

.mobile-hide__text {
  display: none;
}

.mobile-screen__show {
  flex-basis: 85%;
  display: none;
}

.reset body,
.reset div,
.reset dl,
.reset dt,
.reset dd,
.reset ul,
.reset li,
.reset h1,
.reset h2,
.reset h3,
.reset h4,
.reset h5,
.reset h6,
.reset pre,
.reset code,
.reset form,
.reset fieldset,
.reset input,
.reset textarea,
.reset p,
.reset blockquote,
.reset th,
.reset td {
  margin: 0;
  padding: 0;
}

.reset table {
  border-collapse: collapse;
  border-spacing: 0;
}

.reset fieldset,
.reset img,
.reset abbr {
  border: 0;
}

.reset address,
.reset caption,
.reset cite,
.reset code,
.reset dfn,
.reset em,
.reset strong,
.reset th,
.reset var {
  font-style: normal;
  font-weight: normal;
}

.reset ul li {
  list-style: none;
}

.reset caption,
.reset th {
  text-align: left;
}

.reset h1,
.reset h2,
.reset h3,
.reset h4,
.reset h5,
.reset h6 {
  font-size: 100%;
  font-weight: normal;
}

.reset sup {
  vertical-align: text-top;
}

.reset sub {
  vertical-align: text-bottom;
}

.reset input,
.reset textarea,
.reset select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

.reset legend {
  color: #000;
}

.reset article,
.reset aside,
.reset details,
.reset figcaption,
.reset figure,
.reset footer,
.reset header,
.reset hgroup,
.reset menu,
.reset nav,
.reset section,
.reset main {
  display: block;
}

.reset img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

body {
  font-size: 18px;
  position: relative;
  min-width: 320px;
  line-height: 1.6;
  font-family: 'Open Sans', sans-serif;
  background-color: #ffffff;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

.debug {
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.debug > div > div > div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(8.33333% - 30px);
  height: inherit;
  outline: 1px solid #000;
  background: rgba(40, 54, 69, 0.3);
}

@media screen and (max-width: 1200px) {
  .page-header__bottom-wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .proposals__wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .debug > div {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 992px) {
  .main-page__nav {
    width: calc(100% - 30px);
  }

  .main-page__nav {
    position: absolute;
    width: 100%;
    margin: 0;
    margin-top: -17px;
    padding: 0;
    top: 0;
    left: 0;
  }

  .main-page__nav .hamburger {
    display: none;
    position: absolute;
    top: 35px;
    right: 13px;
  }

  .main-nav__wrapper {
    display: none;
  }

  .main-nav__wrapper--open-block {
    display: block;
  }

  .site-list {
    padding: 0;
    flex-direction: column;
    text-align: center;
    font-size: 18px;
    font-weight: 700;
    line-height: 32px;
    text-transform: uppercase;
  }

  .site-list__logo {
    display: block;
    fill: #ffffff;
    max-width: 170px;
    margin-top: 19px;
    margin-right: auto;
    margin-left: 19px;
  }

  .site-list__item {
    min-height: 64px;
    background-color: #283645;
    border-bottom: 1px solid #000;
  }

  .site-list__item a {
    display: block;
    margin-top: 15px;
  }

  .page-header__bottom-wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .iphone-hand__image {
    right: 5%;
  }

  .main-page__nav--no-js .main-nav__wrapper {
    display: block;
  }

  .show-hamburger {
    display: block !important;
  }

  .proposals {
    background-position: center top 317px;
    background-repeat: no-repeat;
    background-size: 100% 520px;
    padding-bottom: 0;
  }

  .proposals__wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .proposals-title {
    font-size: 40px;
  }

  .proposals-title--second {
    max-width: 60%;
    margin-bottom: 95px;
  }

  .proposals-block__wrapper {
    margin-left: 0 auto;
    padding-top: 40px;
    justify-content: flex-end;
    padding-bottom: 25px;
  }

  .proposals-block__item {
    width: calc(58.33333% - 30px);
  }

  .proposals-block__item {
    align-items: flex-start;
    margin-bottom: 8px;
    padding-left: 6%;
  }

  .proposals-block__img {
    display: none;
  }

  .proposals-block__item::after {
    display: none;
  }

  .mood-block::before {
    display: block;
  }

  .proposals-block__title {
    width: 298px;
    margin-bottom: 0;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    text-align: left;
  }

  .proposals-block__subtitle {
    width: 298px;
    min-height: 0;
    text-align: left;
    font-size: 14px;
    font-weight: 400;
  }

  .effect-block__img-wrap {
    margin-left: 30px;
  }

  .effect-block__text-wrap {
    padding-top: 84px;
  }

  .effect-block__title {
    width: 185px;
    font-size: 30px;
    font-weight: 700;
    line-height: 36px;
  }

  .effect-block__description--first {
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 15px;
  }

  .effect-block__description-link {
    font-size: 14px;
    font-weight: 400;
  }

  .reviews {
    padding-top: 93px;
  }

  .reviews::before {
    display: none;
  }

  .price-head__name {
    font-size: 16px;
  }

  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .debug > div {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 720px) {
  .page-header {
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .top-header__wrapper {
    margin-bottom: 396px;
  }

  .main-page__nav .hamburger {
    z-index: 2;
  }

  .site-list {
    position: relative;
    z-index: 1;
  }

  .page-header__bottom-wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .page-header__bottom-wrapper {
    min-width: 100%;
    max-width: 100%;
    padding: 0;
    margin: 0;
    padding-right: 0;
    padding-left: 0;
  }

  .main-page__left-wrapper {
    width: calc(100% - 30px);
  }

  .main-page__left-wrapper {
    background-color: #1d2631;
    min-width: 100%;
    max-width: 100%;
    padding: 0;
    margin: 0;
    padding-right: 0;
    padding-left: 0;
  }

  .iphone-hand__wrapper {
    width: calc(100% - 30px);
  }

  .iphone-hand__wrapper {
    display: none;
  }

