    max-width: 100%;
    padding-top: 29px;
    position: relative;
  }

  .download-block__wrapper::before {
    position: absolute;
    content: "";
    width: 258px;
    height: 289px;
    top: -289px;
    bottom: 0;
    background-image: url(../img/All/iphone-hand.png);
    background-repeat: no-repeat;
    background-size: 258px 289px;
