    margin-bottom: 28px;
  }

  .download-block__img--iphone {
    margin-left: 0;
  }

  .download-block__img--android {
    margin-left: 0;
  }

  .download-block__img--wp {
    margin-left: 0;
  }

  .download-block__deacription {
    width: 70%;
    margin: 0 auto;
    margin-bottom: 35px;
  }

  .proposals {
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: 100% 900px;
    padding-bottom: 0;
  }

  .proposals__wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .proposals-title {
    font-size: 24px;
    font-weight: 300;
  }

  .proposals-title--second {
    margin-bottom: 65px;
  }

  .proposals-block__wrapper {
    margin-bottom: 0;
    padding-bottom: 322px;
  }

  .proposals-block__wrapper::before {
    display: block;
  }

  .proposals-block__item {
    width: calc(100% - 30px);
  }

  .proposals-block__item {
    align-items: center;
    padding-left: 0px;
  }

  .mood-block::before {
    display: none;
  }

